body {
    margin:0;
}

a {
    text-decoration: none;
    color: wheat;
}
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #8B4513;
    padding:8px 12px;
}
.navbar_logo {
    font-size: 24px;
    color :wheat;
}

.navbar_menu {
    display: flex;
    list-style:none;
    padding-left: 0;
}

.navbar_menu li{
    padding: 8px 12px;
}

.navbar_menu li:hover{
    background-color: #311c0e;
    border-radius: 4px;
}

.navbar_toogle {
    display: none;
    position: absolute;
    right: 20px;
    font-size: 24px;
    color:#241104
}

@media screen and (max-width:768px){
    .navbar{
        flex-direction: column;
        align-items: flex-start;
        padding:8px 24px;
    }
    .navbar_menu{
        display: none;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .navbar_menu li{
        width: 100%;
        text-align: center;
    }

    .navbar_toogle{
        display: block;
    }

    #hidden  {
        display: flex;
    }



}